.wptb-table-loading-icon {
  min-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  border: 1px solid #a1a1a1;
  border-radius: 5px;

  svg {
    width: 120px;
    height: 120px;
  }
}

table {
  th,
  td {
    &::after {
      pointer-events: none;
      content: "" !important;
    }
  }
}
