.wptb-mini-picker {
  background: white;
  border: 1px solid #c7d8db;
  border-radius: 5px;

  &-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #c7d8db;
    gap: 20px;

    h2 {
      margin: 0;
      margin-left: 5px;
      font-size: 1rem;
      color: black;
      font-weight: bold;
    }
  }
  &-search {
    position: relative;
    flex-grow: 1;
    display: flex;
    align-items: center;

    svg {
      width: 20px;
      height: 20px;
      position: absolute;
      left: 10px;
    }

    &:has(input:focus) {
      svg {
        color: #0011df;
      }
    }

    input {
      flex-grow: 1;
      padding: 0;
      border-radius: 5px;
      border: 1px solid #e1e1e1;
      background: transparent;
      padding: 5px;
      padding-left: 35px;

      &:focus {
        border: 1px solid #0011df;
        outline: none;
        box-shadow: 0 0 3px #0011df;
      }
    }
  }

  &-tabs {
    display: flex;
    gap: 10px;
    button {
      padding: 5px;
      border: none;
      border-radius: 0;
      background: none;
      cursor: pointer;
      font-size: 0.9rem;

      &.active {
        border-bottom: 2px solid #1000ff;
        color: #1000ff;
      }

      &:hover {
        color: #9690ef;
      }

      svg {
        width: 20px;
        height: 20px;
        margin-bottom: -3px;
        color: gray;
      }
    }
  }

  &-content {
    padding: 10px;
  }
}
