.wptb-builder-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
  background: #f0f0f1;
  padding: 5px;
  overflow: hidden;

  .wptb-app-root {
    margin-left: -10px;
    margin-top: -15px;
    overflow: hidden;

    .wptb-builder-page {
      height: calc(100vh - 40px);
      width: calc(100vw - 40px);
      padding: 0 !important;
      position: static;

      .wptb-builder-sidebar {
        .wptb-control-panel {
          top: 0;
          .wptb-controls {
            height: calc(100vh - 70px);
          }
        }
      }

      .wptb-builder-content {
        .wptb-builder-preview-area {
          padding-top: 100px;
        }
      }

      table {
        th,
        td {
          &::after {
            pointer-events: none;
            content: "" !important;
          }
        }
      }
    }
  }
}
